@import "utilities.css";
@import url("utilities.css");

:root {
  --primary: rgb(0, 110, 9);
  /* --primary: rgb(29, 221, 189); */
  --bgDark: rgb(12, 12, 12);
  --white: rgb(250, 250, 250);
  --secondary: rgb(0, 0, 0);
  --bgLight: rgb(219, 195, 37);
  --darkerGold: rgb(179, 165, 15);
  /* --bgLight: rgb(190, 181, 181);*/
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 1rem;
}

header {
  padding: 0 0;
  margin: 0%;
  background-color: var(--bgDark);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 73% 94%, 0 100%);
}

header nav .left a {
  color: var(--white);
  text-decoration: none;
  margin-right: 2rem;
  text-transform: uppercase;
  transition: all 0.2s ease;
}

header nav .left a:hover {
  color: var(--bgLight);
}

header nav {
  padding: 2rem 0;
}

header nav .logo {
  margin-right: 3rem;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  /* max-width: 1152px; */
  padding: 0 0px;
  margin: 0 0;
}

.hero {
  padding-top: 2rem;
  padding-bottom: 3rem;
  font-family: "Lato", sans-serif;
}

.hero .left img {
  width: 80%;
  max-width: 35vw;
  height: auto;
  object-fit: cover;
}

.hero .right {
  color: var(--white);
  margin-top: 1rem;
}

.hero .right h6 {
  font-size: 1.6rem;
  color: var(--primary);
  margin-bottom: 0.5rem;
}

.hero .right h1 {
  font-size: 4rem;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 2rem;
  transition-duration: 1000ms;
}

.hero .right h2 {
  font-size: 3rem;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 2rem;
  transition-duration: 1000ms;
}

.hero .right h1:hover {
  font-size: 4rem;
  transition-duration: 1000ms;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 2rem;
  color: var(--bgLight);
}

.hero .right h2:hover {
  font-size: 3rem;
  font-weight: 400;
  transition-duration: 1000ms;
  line-height: 1.2;
  margin-bottom: 2rem;
  color: var(--bgLight);
}

.hero .right h1 span {
  color: var(--primary);
}

.hero .right h2 span {
  color: var(--primary);
}

.hero .right h1 span:hover {
  color: var(--bgLight);
}

/* .hero .right h2 span:hover {
    color: var(--bgLight);
} */

.hero .right p {
  font-size: 1.5rem;
  line-height: 1.9;
  margin-bottom: 2rem;
  margin-right: 8rem;
}

section {
  padding: 6rem;
}
section.about .about img {
    width: 35vw;
    border-radius: 1rem;
    height: auto;
    object-fit: cover;
}

section.about h1 {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 600;
}

section.about h1 span {
  color: var(--primary);
}

section.about h3 {
  font-size: 1rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

section.about p {
  font-family: "Lato", sans-serif;
  color: var(--secondary);
  line-height: 1.9rem;
  margin-bottom: 2rem;
  text-align: left;
}

section.about .socials {
  display: flex;
}

section.about .socials a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  margin-right: 0.8rem;
  border-radius: 50%;
}

section.about .socials a:hover {
  background: var(--primary);
}

section.services {
  /* background: rgb(17, 17, 17); */
  background: var(--bgDark);
  padding: 6rem;
}

.services-head {
  /* color: rgb(10, 9, 9); */
  /* text-align: center; */
  margin-bottom: 2rem;
  line-height: 0.5rem;
  color: var(--primary);
}

.services-head h1 {
  font-size: 2rem;
  font-weight: 1000;
  margin-bottom: 1rem;
  text-align: center;
}

.services-head + p {
  color: var(--white);
  font-family: "Lato", sans-serif;
  margin-bottom: 3rem;
  text-align: center;
  font-weight: 400;
}

.card {
  /* height: auto; */
  background: var(--bgLight);
  overflow-wrap: break-word;
  padding: 0 0;
  position: relative;
  text-align: center;
  transition: all 0.8s ease;
  border-radius: 2rem;
}

section.services .card-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 2rem;
  row-gap: 3rem;
}

.card-grid a {
  text-decoration: none;
  /* Remove underline */
  color: black;
  padding: 0 0;
  /* Set text color to black */
}

section.services .card-grid .card {
  background: var(--white);
  padding: 3rem 1.5rem;
  position: relative;
  text-align: center;
  transition: all 0.2s ease;
  height: auto;
}

section.services .card-grid .card img {
  display: block;
  margin: 0 auto 1rem; /* Centers horizontally and adds margin below */
  width: 5rem; /* Adjust size as needed */
  height: auto;
  border-radius: 1rem;
  /* position: relative;
    top: 0;
    left: 50%;
    transform: translateX(-50%); */
  /* color: var(); */
}

section.services .card-grid .card h2 {
  font-weight: 600;
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  transition-duration: 800ms;
}

section.services .card-grid .card p {
  font-family: "Lato", sans-serif;
  color: var(--seconday);
  line-height: 1.6;
  transition-duration: 800ms;
}

section.services .card-grid .card:hover {
  background: var(--darkerGold);
  transition-duration: 500ms;
}

section.services .card-grid .card:hover h2 {
  color: var(--white);
  transition-duration: 500ms;
}

section.services .card-grid .card:hover p {
  color: var(--white);
  transition-duration: 500ms;
}

/* Medium screens - 2 columns */
@media (max-width: 1200px) {
  section.services .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Mobile Responsive Adjustments */
@media (max-width: 800px) {
  section.services .card-grid {
    grid-template-columns: 1fr; /* Stacks cards vertically */
    row-gap: 2rem; /* Increase spacing between cards */
  }

  .services-head h1 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }

  .services-head + p {
    font-size: 1rem;
    margin-bottom: 2rem;
    padding: 0 1rem; /* Add padding for readability on smaller screens */
  }

  section.services {
    padding: 2rem 1rem; /* Reduce padding for smaller screens */
  }

  .card {
    padding: 2rem 1rem; /* Adjust padding within each card */
  }

  .card-grid a {
    margin-bottom: 4rem;
  }
}

/* Center the "Achievements" Heading */
.more .container h1 {
  text-align: center; /* Center only the heading */
  color: var(--primary); /* Keep the color the same */
  font-size: 2rem; /* Adjust font size if needed */
  margin-bottom: 1.5rem; /* Adjust the margin to add spacing below the heading */
}

/* Container for the More Section (remove text centering) */
.more .container {
  max-width: 80%; /* Limit the width for wider screens */
  margin: 0 auto; /* Center container without centering text */
  text-align: left; /* Align content to the left */
}

/* Style the unordered list */
ul {
  list-style-type: disc;
  padding-left: 1.5rem; /* Relative padding for consistency */
  margin: 1rem 0;
  font-size: 1.1rem;
  max-width: 100%; /* Ensure it doesn't exceed container width */
  text-align: left; /* Ensure the text is aligned to the left */
}

/* Individual List Item Adjustments */
li {
  margin-bottom: 1rem;
  line-height: 1.5;
  max-width: 100%;
  text-align: left; /* Ensure the text is aligned to the left */
}

/* Style Nested Lists */
ul ul {
  list-style-type: circle;
  padding-left: 2rem;
  margin-top: 0.5rem;
  max-width: 100%;
  text-align: left; /* Ensure nested items align left */
}

/* Style list item title */
li h3 {
  margin: 0.5rem 0;
  padding-left: 0; /* Remove any extra padding that may cause centering */
  font-weight: bold;
  font-size: 1.5rem;
  text-align: left; /* Ensure the title is aligned to the left */
}





/* Contact css */

.socials {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    background-color: black;
}

.socials a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-right: 4rem;
    border-radius: 50%;
    background: var(--bgLight);
    color: var(--bgDark);
    transition: all 0.3s ease;
}

.socials h1 {
    color: var(--bgLight);
    font-size: 3rem;
    margin-right: 1rem;
    margin-bottom: 4rem;
}

.socials img {
    width: 5rem;
    height: auto;
    border-radius: 0.5rem;
    transition-duration: 400ms;

}

.socials img:hover {
    width: 6rem;
    transition-duration: 400ms;
}






.view-resume-button {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

/* Adjustments for Tablet Screens */
@media (min-width: 601px) and (max-width: 1024px) {
  .hero {
    flex-direction: row;
    align-items: center;
  }

  .hero .right {
    text-align: left;
    margin-left: 2rem;
  }

  section.services .card-grid {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
  }

  .hero .right h1 {
    font-size: 2rem;
  }

  .hero .right h2 {
    font-size: 1.5rem;
  }
}

@media (max-width: 768px) {
  /* Ensure each section stacks vertically */
  .hero,
  .about,
  .services,
  .more,
  .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  /* Adjust flex settings for container classes if any */
  .container {
    width: 100%;
    padding: 5rem; /* Adjust padding if needed */
  }

  /* Images should adjust to the full width of the screen */
  img {
    max-width: 100%;
    height: auto;
  }

  header {
    padding: 3rem 0;
    /* Adjust the top and bottom padding for smaller screens */
  }
}

@media only screen and (device-width: 414px) and (device-height: 896px) and (-webkit-device-pixel-ratio: 2) and (orientation: portrait) {
  /* Styles for iPhone 11 in portrait orientation */
  header {
    padding: 3rem 0;
    /* Adjust the top and bottom padding for smaller screens */
    background-color: var(--bgDark);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 73% 94%, 0 100%);
  }
}

/* Adjustments for Desktop Screens */
@media (min-width: 1090px) {
  section.services .card-grid {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1.5rem;
  }

  .hero .right h1 {
    font-size: 3rem;
  }

  .hero .right h2 {
    font-size: 2rem;
  }
}
